footer{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-top: 100px;
    color: #fff;
    padding-top: 30px;
}

footer .col{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 20px;
    margin-right: 50px;
    margin-left: 50px;
}

footer .logo_footer{
    margin-bottom: 30px;
}

footer h4{
    font-size: 19px;
    font-weight: 700;
    padding-bottom: 20px;
}

footer p{
    font-size: 18px;
    margin: 0 0 8px 0;
}

footer a{
    font-size: 16px;
    text-decoration: none;
    color: #fff;
    margin-bottom: 10px;
}

footer a:hover{
    color: #a0a0a0;
}

footer .socmed{
    margin-top: 20px;
}

footer .socmed i{
    font-size: 25px;
    color: #fff;
    padding-right: 4px;
    cursor: pointer;
}


.copyright{
    text-align: center;
    background-color: rgb(26, 26, 26);
    margin-bottom: -20px;
}

.copyright p{
    color: white;
    font-style: oblique;
}