.form_container{
    border: 2.5px solid rgba(0,0,0);
    width: 320px;
    border-radius: 10px;
    margin-top: 100px;
}

.logInHeader{
    font-weight: bolder;
    margin: 30px 0;
}

.labels{
    width: 100%;
    font-weight: 500;
}

.inputs{
    width: 90%;
    border-radius: 5px;
    border-color: rgba(0,0,0, 0.5);
    margin-bottom: 20px;
}

.loginBtn{
    width: 100px;
    border-radius: 5px;
    margin-bottom: 15px;
    font-weight: bold;
    transition: 0.3s ease;
}

.loginBtn:hover{
    background-color: rgba(0,0,0, 0.05);
    
}